
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
.build-status {
  display: grid;
  grid-template: 1fr / 19% 19% 19% 19% 19%;
}
.keyboard-status {
  margin: 5px;
  padding: 5px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  border-radius: 8px;
}
.keyboard-passed {
  background: #2e8b57;
  color: #eee;
}
.keyboard-failed {
  background: #b22222;
  color: #eee;
  cursor: help;
}
.keyboard-passed > a {
  color: #eee;
}
.keyboard-failed > a {
  color: #eee;
}
.keyboard-filter {
  padding: 10px;
  width: 20%;
  font-size: 1.1rem;
}
.error-pane {
  position: fixed;
  top: 3%;
  display: grid;
  left: 0;
  right: 0;
  margin: auto;
  width: 80%;
  height: 90%;
  grid-template: 50px 1fr / 100%;
  background: #fffff0f5;
  overflow-y: scroll;
  border-radius: 5px;
}
.error-title {
  grid-column: 1;
  grid-row: 1;
}
.error-text {
  grid-column: 1;
  grid-row: 2;
  cursor: pointer;
}
@media (max-width: 640px) {
.build-status {
    display: grid;
    grid-template: 1fr / 48% 48%;
}
.keyboard-filter {
    width: 80%;
}
}
